@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
:root {
  --primary-bg-color: #f6f8ff;
  --secondary-bg-color: #fefefe;
  --text-color: #4b6a9b;
  --joined-text-color: #697c9a;
  --h1-color: #2b3442;
  --blue: #0079ff;
  --logo-text-color: #222731;
  --moon-hover-color: #222731;
  --input-text-color: #222731;
  --box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
  --box-shadow2: 0px 16px 30px rgba(70, 96, 187, 0.198567);
  --search-btn-bg-color: #0079ff;
  --search-btn-bg-hover-color: #60abff;
  --search-btn-text-color: #ffffff;
}

.dark-theme {
  --primary-bg-color: #141d2f;
  --secondary-bg-color: #1e2a47;
  --text-color: #ffffff;
  --joined-text-color: #ffffff;
  --h1-color: #ffffff;
  --logo-text-color: #ffffff;
  --sun-hover-color: #90a4d4;
  --input-text-color: #ffffff;
  --box-shadow: 0;
  --box-shadow2: 0;
}

* {
  box-sizing: border-box;
  font-family: 'Space Mono', sans-serif;
}

body,
#root,
#container {
  margin: 0;
  height: 100vh;
  width: 100vw;
}

#root {
  background: #f6f8ff;
  background: var(--primary-bg-color);
}

#container {
  max-width: 730px;
  width: 87.2%;
  margin: auto;
}

@media (min-width: 768px) {
  #container {
    width: 74.5%;
  }
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto 36px auto;
  padding-top: 31px;
}

.header_logo-text {
  color: var(--logo-text-color);
  font-size: 26px;
  font-weight: bold;
  line-height: 38px;
}

.header_light-dark-mode {
  display: flex;
  color: var(--text-color);
  font-size: 13px;
  font-weight: bold;
  line-height: 20px;
  letter-spacing: 2.5px;
  cursor: pointer;
}

.header_light-dark-mode span {
  padding-right: 16px;
}

/* Sun effect */
.header_light-dark-mode #sun {
  transition: -webkit-transform 0.5s linear;
  transition: transform 0.5s linear;
  transition: transform 0.5s linear, -webkit-transform 0.5s linear;
}
.header_light-dark-mode:hover #sun {
  -webkit-transform: scale(1.1) rotate(90deg);
          transform: scale(1.1) rotate(90deg);
}
.header_light-dark-mode:hover #sun path {
  fill: var(--sun-hover-color);
}
.header_light-dark-mode:hover #sun-text {
  color: var(--sun-hover-color);
}

/* moon effect */
.header_light-dark-mode #moon {
  transition: -webkit-transform 0.5s linear;
  transition: transform 0.5s linear;
  transition: transform 0.5s linear, -webkit-transform 0.5s linear;
}
.header_light-dark-mode:hover #moon {
  -webkit-transform: scale(1.1) rotateY(180deg);
          transform: scale(1.1) rotateY(180deg);
}
.header_light-dark-mode:hover #moon path {
  fill: var(--moon-hover-color);
}
.header_light-dark-mode:hover #moon-text {
  color: var(--moon-hover-color);
}

/* tablet */
@media (min-width: 768px) {
  header {
    padding-top: 142px;
  }
}

/* destop */
@media (min-width: 980px) {
  header {
    padding-top: 144px;
  }
}

#form-container {
  width: 100%;
  height: 60px;
  margin-bottom: 16px;
}

#form-container > form {
  position: relative;
  width: 100%;
  height: 100%;
}
#form-container > form > .search-magnifier {
  position: absolute;
  top: 20px;
  left: 16px;
  height: 20px;
  width: 20px;
}
#form-container > form > .input {
  background-color: var(--secondary-bg-color);
  color: var(--input-text-color);
  font-family: 'Space Mono';
  font-size: 13px;
  line-height: 25px;
  width: 100%;
  height: 100%;
  padding: 18px 98px 18px 45px;
  border: none;
  outline: none;
  box-shadow: var(--box-shadow);
  border-radius: 15px;
}
#form-container > form > .input::-webkit-input-placeholder {
  color: var(--text-color);
}
#form-container > form > .input:-ms-input-placeholder {
  color: var(--text-color);
}
#form-container > form > .input::placeholder {
  color: var(--text-color);
}
#form-container > form > .no-results {
  position: absolute;
  top: -22px;
  right: 13px;
  color: #f74646;
  font-weight: bold;
  font-size: 15px;
  line-height: 22px;
}
#form-container > form > .search-btn {
  background: var(--search-btn-bg-color);
  color: var(--search-btn-text-color);
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: bold;
  line-height: 21px;
  position: absolute;
  width: 84px;
  height: 46px;
  top: 7px;
  right: 7px;
}
#form-container > form > .search-btn:hover {
  background: var(--search-btn-bg-hover-color);
}

/* tablet */
@media (min-width: 768px) {
  #form-container {
    height: 69px;
    margin-bottom: 24px;
  }
  #form-container > form > .search-magnifier {
    height: 24px;
    width: 24px;
    top: 23px;
    left: 32px;
  }
  #form-container > form > .input {
    padding: 22px 239px 22px 80px;
    font-size: 18px;
  }
  #form-container > form > .search-btn {
    width: 106px;
    height: 50px;
    top: 9.5px;
    right: 10px;
  }
}

/* destop */
@media (min-width: 980px) {
  #form-container > form > .no-results {
    top: 24px;
    right: 140px;
  }
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.user-background {
  background: var(--secondary-bg-color);
  box-shadow: var(--box-shadow2);
  border-radius: 15px;
}
.user {
  width: 85.3%;
  margin: 0 auto;
}
.user__title-container {
  display: flex;
  padding-top: 32px;
  margin-bottom: 33px;
}
.user__avatar-container {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 19px;
}
.user__avatar {
  height: 100%;
  width: 100%;
}
.user__name {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: var(--h1-color);
}
.user__login a {
  font-size: 13px;
  line-height: 19px;
  color: #0079ff;
}
.user_joined-day {
  font-size: 13px;
  line-height: 19px;
  color: var(--joined-text-color);
}
.user__bio {
  font-size: 13px;
  line-height: 25px;
  color: var(--text-color);
  margin-bottom: 23px;
}
.user__repos-followers-following-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: var(--primary-bg-color);
  border-radius: 10px;
  height: 85px;
  margin-bottom: 24px;
}
.user__bio,
.user__repos-followers-following-container,
.user__links-container {
  max-width: 480px;
}
.user__repos,
.user__followers,
.user__following {
  text-align: center;
}
.user__repos__name,
.user__followers__name,
.user__following__name {
  font-size: 11px;
  line-height: 16px;
  color: var(--text-color);
  margin-bottom: 8px;
}
.user__repos__num,
.user__followers__num,
.user__following__num {
  font-size: 16px;
  font-weight: bold;
  line-height: 24px;
  color: var(--h1-color);
}
.user__location,
.user__blog,
.user__twitter,
.user__company {
  display: flex;
  margin-bottom: 16px;
}
.user__Link-icons {
  margin-right: 13px;
}
.user__Link-icons path {
  fill: var(--text-color);
}
.link-not-avalible,
.link-not-avalible span,
.link-not-avalible a {
  pointer-events: none !important;
  color: #ccc !important;
}
.link-not-avalible svg path {
  fill: #ccc !important;
}
.user__links-container {
  padding-bottom: 48px;
}
.user__links-container span,
.user__links-container a {
  font-size: 13px;
  line-height: 19px;
  color: var(--text-color);
}

/* tablet */
@media (min-width: 768px) {
  .user {
    width: 86.02%;
  }
  .user__title-container {
    padding-top: 40px;
    margin-bottom: 24px;
  }
  .user__avatar-container {
    width: 117px;
    height: 117px;
    margin-right: 41px;
  }
  .user__name {
    font-size: 26px;
    line-height: 39px;
    margin-bottom: 2px;
  }
  .user__login {
    margin-bottom: 4px;
  }
  .user__login a {
    font-size: 16px;
    line-height: 24px;
  }
  .user_joined-day {
    font-size: 15px;
    line-height: 22px;
  }
  .user__names-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .user__bio {
    font-size: 15px;
    line-height: 25px;
    margin-bottom: 32px;
  }
  .user__repos-followers-following-container {
    justify-content: flex-start;
    margin-bottom: 30px;
  }
  .user__repos {
    margin: 0 99px 0 32px;
  }
  .user__followers {
    margin-right: 82px;
  }
  .user__repos__name,
  .user__followers__name,
  .user__following__name {
    font-size: 13px;
    line-height: 19px;
  }
  .user__repos__num,
  .user__followers__num,
  .user__following__num {
    font-size: 22px;
    line-height: 33px;
  }
  .user__links-container {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 40px;
  }
  .user__location,
  .user__blog,
  .user__twitter,
  .user__company {
    flex-basis: 50%;
  }
  .user__location {
    order: 1;
  }
  .user__blog {
    order: 3;
  }
  .user__twitter {
    order: 2;
  }
  .user__company {
    order: 4;
  }
}

/* destop */
@media (min-width: 980px) {
  .user__avatar-container {
    margin-right: 37px;
  }
  .user__names-container {
    position: relative;
    flex-grow: 2;
  }
  .user_joined-day {
    position: absolute;
    top: 32.5px;
    right: 0;
  }
  .user__bio,
  .user__repos-followers-following-container,
  .user__links-container {
    margin-left: auto;
  }
  .user__title-container {
    margin: 0;
  }
}

